<div class="container"><h1 class="text-center my-5">{{ appName }}</h1><div class="card"><div class="card-header"><ul class="nav nav-tabs card-header-tabs"><li class="nav-item" v-for="item in menuItems"><router-link class="nav-link" :class="{active: currentRouteName === item.to.name}" :to="item.to">{{ item.label }}</router-link></li><li class="nav-item" v-if="accountIsLoggedIn"><a class="nav-link" @click.prevent="onLogoutClick" href="#">Logout</a></li></ul></div><div class="card-body"><router-view></router-view></div></div></div>